.realroot{
  width: 100vw;
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
}
.root {
  width: 550px;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  flex-wrap: wrap;
  align-content:center;
}

.main {
  background-color: #f1f1f1;
  border-radius: 8px;
  padding: 32px;
  width: 550px;
  margin-top: 10px;
}

.main2 {
  background-color: #f1f1f1;
  border-radius: 8px;
  padding: 32px;
  padding-top: 0;
  width: 550px;
  margin-top: 10px;
}
.h1 {
  text-align: center;
  margin-bottom: 24px;
}

.filesContainer {
  margin-bottom: 24px;
}

.dropContainer {
  border: 2px dashed #ccc;
  padding: 16px;
  border-radius: 8px;
  text-align: center;
  cursor: pointer;
}

.dragging {
  background-color: #f9f9f9;
}

.fileInput {
  display: block;
  margin-top: 8px;
  font-size: 14px;
  color: #555;
}

.fileInputLabel {
  display: block;
  width: 100%;
  margin-top: 16px;
  padding: 8px;
  text-align: center;
  background-color: #ccc;
  border-radius: 4px;
  cursor: pointer;
}

.fileInputContainer {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: #f9f9f9;
  padding: 8px;
  border-radius: 4px;
}

.fileName {
  flex: 1;
  margin-left: 8px;
  font-size: 14px;
  color: #555;
}

.fileInput {
  position: absolute;
  left: -9999px;
}

.fileInputLabel {
  display: block;
  width: 100%;
  margin-top: 16px;
  padding: 8px;
  text-align: center;
  background-color: #ccc;
  border-radius: 4px;
  cursor: pointer;
}

.disabled {
  width: 100%;
  background-color: #ccc;
  cursor: not-allowed;
  padding: 8px 16px;
  color: #fff;
  border: none;
  border-radius: 4px;
}

.code {
  background-color: #fff;
  border: 1px solid #ccc;
  border-radius: 8px;
  padding: 16px;
  margin-bottom: 24px;
}

.codeDetail {
  text-align: center;
}

.codeTitle {
  font-weight: bold;
  margin-bottom: 8px;
}

.codes {
  background-color: #f1f1f1;
  padding: 4px 8px;
}

.downloadContainer {
  text-align: left;
  margin-bottom: 24px;
}

.downloadContainer h2 {
  margin-bottom: 8px;
}


.downInput{
  display: block;
  width: 95%;
  padding: 8px;
  margin-bottom: 8px;
  border: 1px solid #ccc;
  border-radius: 4px;
}
.buttonGroup {
  display: flex;
  justify-content: center;
}

.buttonGroup .button {
  margin-right: 8px;
}

.button {
  width: 100%;
  padding: 8px 16px;
  background-color: #9c27b0;
  color: #fff;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.button:hover {
  background-color: #7b1fa2;
}
